import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { actions } from '../../redux';
import PropTypes from 'prop-types';
import './Registration.css';
import { API } from '../../api';
import { Button } from './../Button';

class _Registration extends Component {

  constructor(props) {
    super(props);
    this.state = {
      isValid: false,
      phoneInvalid: false,
      phoneVisited: false
    };
    this.nameElement = null;
    this.phoneElement = null;
  }


  componentDidMount() {
    const rootElement = document.querySelector('#Registration');
    this.nameElement = rootElement.querySelector('.Registration__name');
    this.phoneElement = rootElement.querySelector('.Registration__phone');

    this.processValidation();

    this.nameElement.focus();
  }


  render() {
    return <div id="Registration" className="block Registration">
      <form onSubmit={this.onSubmit.bind(this)}>
        <div>
          <input type="text"
            className="Registration__input Registration__name"
            onKeyUp={this.onInputChange.bind(this)}
            placeholder="Ваши фамилия и имя"
          ></input>
        </div>
        <div className="indent_vert">
          <input type="text"
            className="Registration__input Registration__phone"
            onKeyUp={this.onInputChange.bind(this)}
            onBlur={this.setPhoneVisited.bind(this)}
            placeholder="Номер телефона"
          ></input>
          <div className="indent_hor_left indent_vert_half Registration__hint">
            Для связи в случае выигрыша
          </div>
        </div>
        <div className="indent_vert">
          <Button text="Начать!" type="submit" isDisabled={!this.state.isValid}/>
        </div>
      </form>
    </div>;
  }


  onSubmit(event) {
    event.preventDefault();
    if (this.state.isValid) {
      const userInfo = this.getUserInfo();
      this.props.actions.fetchQuestion(userInfo);
    }
  }


  onInputChange(event) {
    if (event.target.classList.contains('Registration__phone') && event.key === 'Enter') {
      this.setPhoneVisited();
    }
    this.processValidation();
  }


  goToResults() {
    this.props.actions.fetchResults();
  }


  setPhoneVisited() {
    this.setState({
      phoneVisited: true
    });
  }


  getUserInfo() {
    const name = this.nameElement.value || '';
    const phone = this.phoneElement.value || '';
    return API.generateUserInfo(name, phone);
  }


  isUserInfoValid(userInfo) {
    return userInfo.name.length > 1 && this.isPhoneValid(userInfo.phone);
  }


  processValidation() {
    const userInfo = this.getUserInfo();
    const isValid = this.isUserInfoValid(userInfo);
    const phoneInvalid = userInfo.phone && !this.isPhoneValid(userInfo.phone);
    this.setState({
      phoneInvalid,
      isValid
    });
  }


  isPhoneValid(phone) {
    return /^[+]?[\d-()\s]{7,}$/.test(phone);
  }

}


_Registration.propTypes = {
  actions: PropTypes.object
};


function mapStateToProp(state) {
  return {
  };
}


function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(actions, dispatch)
  };
}


export const Registration = connect(mapStateToProp, mapDispatchToProps)(_Registration);



