import { questions } from './questions-mock';
import { getMockResults, addMockResult, removeMockResult } from './results-mock';
import { getUserResults } from './user-results-mock';


function delay(timeout) {
  return new Promise(resolve => setTimeout(resolve, timeout || 0));
}


const cache = {
  adminPassword: 'odkl18jpoint',
  questions: null,
  questionsNumber: 0
};


export const API = {

  askAdminPassword() {
    return new Promise((resolve, reject) => {
      const password = prompt('Пожалуйста, введите админский пароль');
      if (cache.adminPassword === password) {
        resolve();
      } else {
        reject();
      }
    });
  },


  removeResult(result) {
    return delay().then(() => removeMockResult(result));
  },


  fetchResults() {
    return delay().then(getMockResults);
  },


  fetchQuestion(userInfo, prevQuestion) {
    return delay()
      .then(() => API.fetchQuestions())
      .then((questions) => {
        const currentNum = prevQuestion ? prevQuestion.num : 0;
        const nextNum = currentNum + 1;
        const question = questions.filter(q => q.num === nextNum)[0] || null;
        return question;
      });
  },


  fetchQuestions() {
    if (cache.questions) {
      return Promise.resolve(cache.questions);
    }
    return delay().then(() => {
      cache.questions = questions;
      cache.questionsNumber = questions.length;
      return questions;
    });
  },


  applyAnswer(userInfo, question, answer) {
    return delay().then(() => {
      const updatedUserInfo = Object.assign({}, userInfo);
      const reactionIndex = Math.floor(Math.random() * answer.reactions.length);
      const reaction = answer.reactions[reactionIndex];
      updatedUserInfo.aValue += reaction.aValue;
      updatedUserInfo.bValue += reaction.bValue;
      return {
        userInfo: updatedUserInfo,
        reaction
      };
    });
  },


  fetchUserResult(userInfo) {
    return delay().then(() => {
      const updatedUserInfo = Object.assign({}, userInfo);
      updatedUserInfo.score = userInfo.aValue + userInfo.bValue;
      addMockResult(updatedUserInfo);

      const userResult = getUserResults(updatedUserInfo);
      return {
        userInfo: updatedUserInfo,
        userResult
      };
    });
  },


  getQuestionsNumber() {
    return cache.questionsNumber;
  },


  _getUserInfoId(name, phone) {
    const phoneDigits = phone.replace(/\D+/g, '');
    const preparedName = name.trim().toLowerCase().replace(/\s/g, '');
    return `${preparedName}__${phoneDigits}`;
  },


  generateUserInfo(name, phone) {
    return {
      id: API._getUserInfoId(name, phone),
      name,
      phone,
      score: 0,
      aValue: 0,
      bValue: 0
    };
  }

};



