import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import './AppHeader.css';
import { appHeaderMode } from './../../helpers';


export class _AppHeader extends Component {


  renderScore(scoreLabel, scoreValue, scoreDiff) {
    const diffModifier = scoreDiff < 0
      ? 'AppHeader__score-diff_negative'
      : (scoreDiff > 0 ? 'AppHeader__score-diff_positive' : '');
    const diffClassName = `AppHeader__score-diff ${diffModifier}`;

    return <span className="AppHeader__score">
      <span className="AppHeader__score-label">{ scoreLabel }: </span>
      <span className="AppHeader__score-value">{ scoreValue } </span>
      {
        scoreDiff !== null && <span className={diffClassName}>
          { scoreDiff < 0 ? '-' : (scoreDiff > 0 ? '+' : '') }
          { Math.abs(scoreDiff) }
        </span>
      }
    </span>;
  }


  renderScores(headerScoresData) {
    return <React.Fragment>
      { headerScoresData.finished
        ? <div/>
        : <div className="AppHeader__question-progress">
          { headerScoresData.currentQuestionNumber } / { headerScoresData.questionsNumber }
        </div>
      }

      <div className="AppHeader__scores">
        { this.renderScore('Хайп', headerScoresData.aValue, headerScoresData.aValueDiff) }
        { this.renderScore('Качество', headerScoresData.bValue, headerScoresData.bValueDiff) }
      </div>
    </React.Fragment>;
  }


  renderFullscreenButton() {
    if (this.props.isInFullscreen) {
      return null;
    }

    return <span className="AppHeader__fullscreen"
      onClick={this.props.goToFullscreen}
    >
      Развернуть
    </span>;
  }


  renderByMode(mode, headerScoresData) {
    switch (this.props.mode) {
      case appHeaderMode.SCORES:
        return this.renderScores(headerScoresData);
      case appHeaderMode.LOGO_WITH_FULLSCREEN:
        return this.renderFullscreenButton();
      default:
        return null;
    }
  }


  render() {
    const mode = this.props.mode;
    const className = `block AppHeader AppHeader_mode_${mode}`;
    return <header className={className}>
      { this.renderByMode(mode, this.props.headerScoresData) }
    </header>;
  }

}


_AppHeader.propTypes = {
  isInFullscreen: PropTypes.bool,
  goToFullscreen: PropTypes.func,
  mode: PropTypes.string,
  headerScoresData: PropTypes.object
};


function mapStateToProp(state) {
  return {
    mode: state.appHeaderMode || appHeaderMode.LOGO,
    headerScoresData: state.headerScoresData
  };
};


export const AppHeader = connect(mapStateToProp, null)(_AppHeader);



