import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { actions } from '../../redux';
import PropTypes from 'prop-types';
import './Results.css';
import { Button } from './../Button';

const introText = 'Игра «Построй свой сервис на Java!»';

const numberOfTopPlayers = 10;


class _Results extends Component {

  renderResults(results) {
    const topPlayers = this.getTopResults(results);
    const hasTops = topPlayers.length > 0;

    return <React.Fragment>
      { hasTops && <table className="Results__table">
        <thead>
          <tr>
            <td className="Results__table-head-title">
              Топ игроков
            </td>
            <td className="Results__table-head-scores">
              Очки
            </td>
          </tr>
          <tr>
            <td colSpan="2">
              <div className="Results__table-head-delimiter"></div>
            </td>
          </tr>
        </thead>
        <tbody>
          { topPlayers.map((player, index) => <tr key={player.id}>
              <td className="Results__table-result-name">
                <span className="Results__result-index">
                  { index + 1 }
                </span>
                <span className="Results__result-name">
                  { player.name }
                </span>
              </td>
              <td className="Results__table-result-score">
                { player.score }
              </td>
            </tr>)
          }
        </tbody>
      </table>
      }
      <div className="indent_vert Results__go-to-admin-container">
          <span className="Results__go-to-admin"
            onClick={this.goToAdmin.bind(this)}
          >
            администрирование
          </span>
      </div>
    </React.Fragment>;
  }


  renderActions() {
    return <React.Fragment>
      <Button text="Играть" onPress={this.startGame.bind(this)}/>
    </React.Fragment>;
  }


  renderIntro() {
    return <div className="Results__intro">{ introText }</div>;
  }


  render() {
    return <div className="block Results">
      { this.renderIntro() }
      <div className="indent_vert">
        { this.renderActions() }
      </div>
      <div className="indent_vert">
        { this.renderResults(this.props.results) }
      </div>
    </div>;
  }


  startGame() {
    this.props.actions.registerInGame();
  }


  getTopResults(results) {
    const safeRestuls = results || [];
    const topResults = safeRestuls.slice()
      .sort((resultA, resultB) => resultB.score - resultA.score)
      .slice(0, numberOfTopPlayers);
    return topResults;
  }


  goToAdmin() {
    this.props.actions.administrateResults();
  }

}


_Results.propTypes = {
  actions: PropTypes.object,
  waiting: PropTypes.bool,
  results: PropTypes.array
};


function mapStateToProp(state) {
  return {
    waiting: state.waitingForResults,
    results: state.results
  };
}


function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(actions, dispatch)
  };
}


export const Results = connect(mapStateToProp, mapDispatchToProps)(_Results);



