import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './UserResult.css';
import { MainImage } from './../MainImage';
import { Button } from './../Button';


export class UserResult extends Component {

  render() {
    const { userResult } = this.props;

    return <div className="block UserResult">
      <MainImage src={userResult.img} sticker={userResult.sticker}/>

      <div className="indent_vert UserResult__title">
        { userResult.title }
      </div>

      <div className="indent_vert UserResult__text">
        { userResult.text }
      </div>

      <div className="indent_vert_double UserResult__actions">
        <Button text="Результаты" onPress={this.props.onShowResults}/>
        <Button text="Играть ещё раз" onPress={this.props.onTryAgain}/>
      </div>
    </div>;
  }


  selectAnswer(event) {
    const answerIndex = event.target.dataset.index;
    const answer = this.props.question.answers[answerIndex];
    this.props.onSelectAnswer(answer);
  }

}


UserResult.propTypes = {
  userInfo: PropTypes.object,
  userResult: PropTypes.object,
  onShowResults: PropTypes.func,
  onTryAgain: PropTypes.func
};



