const resultsStorageKey = 'results-mock_1';


function _getMockResults() {
  const rawStoredResults = localStorage.getItem(resultsStorageKey) || '[]';
  const results = JSON.parse(rawStoredResults);
  return results;
}


let results = _getMockResults();


function saveResults(resultsToSave) {
  const resultsToStore = JSON.stringify(resultsToSave);
  localStorage.setItem(resultsStorageKey, resultsToStore);
  // Генерируем новый массив, чтобы реакт увидел обновление этого массива.
  results = results.slice();
}


export function getMockResults() {
  return results;
}


export function removeMockResult(result) {
  const indexOf = results.reduce((index, item, currentIndex) => {
    if (item.id === result.id) {
      return currentIndex;
    }
    return index;
  }, -1);
  if (indexOf >= 0) {
    results.splice(indexOf, 1);
    saveResults(results);
  }
}


export function addMockResult(userInfo) {
  const prevUserInfo = results.filter(ui => ui.id === userInfo.id)[0];
  if (prevUserInfo) {
    prevUserInfo.aValue = userInfo.aValue;
    prevUserInfo.bValue = userInfo.bValue;
    prevUserInfo.score = userInfo.score;
  } else {
    const userInfoCopy = Object.assign({}, userInfo);
    results.push(userInfoCopy);
  }
  saveResults(results);
}



