import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { actionTypes, actions } from '../../redux';
import PropTypes from 'prop-types';
import './App.css';
import { Results } from './../Results';
import { AppHeader } from './../AppHeader';
import { Registration } from './../Registration';
import { Waiter } from './../Waiter';
import { Question } from './../Question';
import { AnswerResult } from './../AnswerResult';
import { UserResult } from './../UserResult';
import { AdminResults } from './../AdminResults';
import { StickersList } from './../StickersList';


class _App extends Component {

  constructor(props) {
    super(props);

    this.state = {
      isInFullscreen: false
    };

    this.processFullscreenEvent = this.processFullscreenEvent.bind(this);
  }


  componentWillMount() {
    this.props.actions.fetchResults();

    document.addEventListener('fullscreenchange', this.processFullscreenEvent);
  }


  componentWillUnmount() {
    document.removeEventListener('fullscreenchange', this.processFullscreenEvent);
  }


  renderByAction() {
    const { action, question, reaction, userInfo, userResult } = this.props;

    switch (action) {
      case actionTypes.SHOW_STICKERS:
        return <StickersList/>;
      case actionTypes.ADMINISTRATE_RESULTS:
        return <AdminResults/>;
      case actionTypes.FETCH_RESULTS:
      case actionTypes.SHOW_RESULTS:
        return <Results/>;
      case actionTypes.REGISTER_IN_GAME:
        return <Registration/>;
      case actionTypes.FETCH_QUESTION:
        return <Waiter text="wait for a question"/>;
      case actionTypes.APPLY_ANSWER:
        return <Waiter text="applying answer"/>;
      case actionTypes.FETCH_USER_RESULT:
        return <Waiter text="wait for a user results"/>;
      case actionTypes.SHOW_QUESTION:
        return <Question question={question}
          onSelectAnswer={this.onSelectAnswer.bind(this)}
        />;
      case actionTypes.SHOW_ANSWER_RESULT:
        return <AnswerResult reaction={reaction}
          onNextQuestion={this.onNextQuestion.bind(this)}
        />;
      case actionTypes.SHOW_USER_RESULT:
        return <UserResult userInfo={userInfo}
          userResult={userResult}
          onShowResults={this.onShowResults.bind(this)}
          onTryAgain={this.onTryAgain.bind(this)}
        />;
      default:
        return 'unknown state!';
    }
  }


  render() {
    return <main className="App">
      <div className="App__container">
        <AppHeader isInFullscreen={this.state.isInFullscreen}
          goToFullscreen={this.goToFullscreen.bind(this)}
        />
        { this.renderByAction() }
      </div>
    </main>;
  }


  onNextQuestion() {
    this.props.actions.fetchQuestion(this.props.userInfo, this.props.question);
  }


  onSelectAnswer(answer) {
    this.props.actions.applyAnswer(this.props.userInfo, this.props.question, answer);
  }


  onShowResults() {
    this.props.actions.fetchResults();
  }


  onTryAgain() {
    this.props.actions.tryAgain(this.props.userInfo);
  }


  goToFullscreen() {
    const root = document.querySelector('#root');
    root.requestFullscreen();
  }


  processFullscreenEvent() {
    const isInFullscreen = !!document.fullscreenElement;
    if (this.isInFullscreen !== isInFullscreen) {
      this.setState({ isInFullscreen });
    }
  }

}


_App.propTypes = {
  actions: PropTypes.object,
  action: PropTypes.string,
  answer: PropTypes.object,
  reaction: PropTypes.object,
  userInfo: PropTypes.object,
  userResult: PropTypes.object,
  question: PropTypes.object
};


function mapStateToProp(state) {
  return {
    action: state.action,
    reaction: state.reaction,
    answer: state.answer,
    userInfo: state.userInfo,
    userResult: state.userResult,
    question: state.question
  };
}


function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(actions, dispatch)
  };
}


export const App = connect(mapStateToProp, mapDispatchToProps)(_App);



