import { actionTypes } from './actionTypes';
import { initialState } from './initialState';
import { appHeaderMode } from './../helpers';

export function reducer(state = initialState, action) {
  const newState = {
    action: action.type
  };

  function assign(ext) {
    return Object.assign(newState, ext);
  }

  switch (action.type) {

    case actionTypes.ADMINISTRATE_RESULTS: {
      return assign({
        results: action.results
      });
    }


    case actionTypes.FETCH_RESULTS: {
      return assign({
        appHeaderMode: appHeaderMode.LOGO_WITH_FULLSCREEN,
        waitingForResults: true
      });
    }


    case actionTypes.SHOW_RESULTS: {
      if (state.action !== actionTypes.FETCH_RESULTS) {
        return state;
      }
      return assign({
        appHeaderMode: appHeaderMode.LOGO_WITH_FULLSCREEN,
        results: action.results
      })
    }


    case actionTypes.APPLY_ANSWER:
    case actionTypes.SHOW_ANSWER_RESULT:
    case actionTypes.FETCH_USER_RESULT:
    case actionTypes.SHOW_USER_RESULT:
    case actionTypes.FETCH_QUESTION:
    case actionTypes.SHOW_QUESTION: {
      if (action.type === actionTypes.SHOW_QUESTION && state.action !== actionTypes.FETCH_QUESTION) {
        return state;
      }
      if (action.type === actionTypes.SHOW_ANSWER_RESULT && state.action !== actionTypes.APPLY_ANSWER) {
        return state;
      }

      const { userInfo, answer, question, reaction } = action;

      const questionsNumber = action.questionsNumber
        || (state.headerScoresData ? state.headerScoresData.questionsNumber : 0);

      return assign({
        appHeaderMode: appHeaderMode.SCORES,
        headerScoresData: {
          questionsNumber,
          currentQuestionNumber: question ? question.num : 1,
          aValue: userInfo.aValue,
          aValueDiff: reaction ? reaction.aValue : null,
          bValue: userInfo.bValue,
          bValueDiff: reaction ? reaction.bValue : null,
          finished: !question
        },
        userInfo,
        userResult: action.userResult,
        question,
        reaction: reaction,
        answer
      });
    }


    default: {}

  }

  return newState;

}



