import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { actions } from '../../redux';
import PropTypes from 'prop-types';
import './StickersList.css';
import { Button } from './../Button';


function getStickersModifiers(modPrefix, minNum, maxNum) {
  const mainClass = 'Sticker';
  const stickers = [];
  for(let i = minNum; i <= maxNum; i++) {
    const id = `${modPrefix}_${i}`;
    const className = `${mainClass} ${mainClass}_${id}`;
    stickers.push({
      className,
      id
    });
  }
  return stickers;
}

const stickersClasses = [
  ...getStickersModifiers('fox', 100, 119),
  ...getStickersModifiers('dog', 100, 115)
];


class _StickersList extends Component {

  render() {
    return <div className="block StickersList">
      <div className="StickersList__list">
        { stickersClasses.map(sticker => <div key={sticker.id} className="StickersList__sticker">
            <div className="StickersList__container">
              <span className={sticker.className}></span>
            </div>
            <div className="StickersList__className">
              { sticker.id }
            </div>
          </div>)
        }
      </div>
      <div>
        <Button text="К результатам" onPress={this.backToResults.bind(this)}/>
      </div>
    </div>;
  }


  backToResults() {
    this.props.actions.fetchResults();
  }

}


_StickersList.propTypes = {
  actions: PropTypes.object
};


function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(actions, dispatch)
  };
}


export const StickersList = connect(null, mapDispatchToProps)(_StickersList);



