import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './Waiter.css';


export class Waiter extends Component {

  render() {
    const text = this.props.text || 'waiting';
    return <span className="Waiter">
      { text } ...
    </span>;
  }

}


Waiter.propTypes = {
  text: PropTypes.string
};



