import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './Question.css';
import { MainImage } from './../MainImage';


export class Question extends Component {

  render() {
    const question = this.props.question;
    const hasImage = !!question.img;
    const textContainerClass = `Question__text` + (hasImage ? ' indent_vert' : '');

    return <React.Fragment>
      <div className="block Question__question">
        { hasImage && <MainImage src={question.img}/> }

        <div className={textContainerClass}>
          { question.text }
        </div>
      </div>

      <div className="Question__answers">
        <ul className="block Question__answers-list">
          {
            question.answers.map((answer, index) => <li key={ index }
              className="indent_vert_half block_half Question__answer"
              onClick={this.selectAnswer.bind(this)}
              data-index={ index }
            >
              { answer.text }
            </li>)
          }
        </ul>
      </div>
    </React.Fragment>;
  }


  selectAnswer(event) {
    const answerIndex = event.target.dataset.index;
    const answer = this.props.question.answers[answerIndex];
    this.props.onSelectAnswer(answer);
  }

}


Question.propTypes = {
  question: PropTypes.object,
  onSelectAnswer: PropTypes.func
};



