import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { actions } from '../../redux';
import PropTypes from 'prop-types';
import './AdminResults.css';
import { Button } from './../Button';


class _AdminResults extends Component {

  render() {
    return <div className="block AdminResults">
      <div className="AdminResults__header">
        Результаты
      </div>
      <ul className="AdminResults__list">
        { this.props.results.map((result, index) => <li className="indent_vert_half AdminResults__result"
            key={result.id}
          >
            <span className="AdminResults__index AdminResults__info-item">
              { index + 1 }
            </span>
            <span className=" AdminResults__info-item">
              Очки:&nbsp;
              <span className="AdminResults__score">
                { result.score }
              </span>
            </span>
            <span className="AdminResults__info-item">
              { result.name }
            </span>
            <span className="AdminResults__info-item">
              { result.phone }
            </span>
            <span className="AdminResults__remove AdminResults__info-item"
              data-index={index}
              onClick={this.removeResult.bind(this)}
            >
              удалить
            </span>
          </li>)
        }
      </ul>
      <div className="indent_vert">
        <Button text="Вернуться к обычным результатам"
          onPress={this.backToRegularResults.bind(this)}
        />
        <Button text="Стикеры"
          onPress={this.goToStickers.bind(this)}
        />
      </div>
    </div>;
  }


  goToStickers() {
    this.props.actions.showStickers();
  }


  backToRegularResults() {
    this.props.actions.fetchResults();
  }


  removeResult(event) {
    const resultIndex = event.target.dataset.index;
    const result = this.props.results[resultIndex];
    const confirmMsg = `Вы действительно хотите удалить результат пользователя\n`
      + `${result.name} (${result.phone}), очков ${result.score}?`;
    if (window.confirm(confirmMsg)) {
      this.props.actions.removeResult(result);
    }
  }

}


_AdminResults.propTypes = {
  results: PropTypes.array
};


function mapStateToProp(state) {
  return {
    results: state.results
  };
}


function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(actions, dispatch)
  };
}


export const AdminResults = connect(mapStateToProp, mapDispatchToProps)(_AdminResults);



