import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './MainImage.css';


export class MainImage extends Component {

  render() {
    const stickerClass = `Sticker Sticker_${this.props.sticker}`;
    return <div className="MainImage">
      { this.props.src && <img alt="" src={this.props.src} className="MainImage__img"/> }
      { this.props.sticker && <span className={stickerClass}/> }
    </div>;
  }

}


MainImage.propTypes = {
  src: PropTypes.string,
  sticker: PropTypes.string
};



