import { actionTypes } from './actionTypes';
import { API } from '../api';


function getApiErrorLogger(cmdName) {
  return (error) => {
    console.log('API error while calling', error);
    alert(`API error while calling ${cmdName}:\n ${error}`);
  }
}


export const actions = Object.freeze({

  showStickers() {
    return dispatch => dispatch({
      type: actionTypes.SHOW_STICKERS
    });
  },

  administrateResults() {
    return dispatch => {
      API.askAdminPassword()
        .then(() => API.fetchResults())
        .then(results => dispatch({
          results,
          type: actionTypes.ADMINISTRATE_RESULTS
        }))
        .catch(() => {});
    };
  },

  removeResult(result) {
    return dispatch => {
      API.removeResult(result)
        .then(() => actions.administrateResults()(dispatch))
        .catch(() => actions.fetchResults()(dispatch));
    };
  },

  registerInGame() {
    return dispatch => {
      dispatch({ type: actionTypes.REGISTER_IN_GAME });
    };
  },


  fetchQuestion(userInfo, prevQuestion) {
    return dispatch => {
      dispatch({
        type: actionTypes.FETCH_QUESTION,
        answer: null,
        userInfo,
        question: prevQuestion
      });
      API.fetchQuestion(userInfo, prevQuestion)
        .then(question => {
          if (question) {
            actions.showQuestion(userInfo, question)(dispatch);
          } else {
            actions.fetchUserResult(userInfo)(dispatch);
          }
        })
        .catch(getApiErrorLogger('fetchQuestion'));
    };
  },


  showQuestion(userInfo, question) {
    return dispatch => dispatch({
        type: actionTypes.SHOW_QUESTION,
        questionsNumber: API.getQuestionsNumber(),
        userInfo,
        question
    });
  },


  applyAnswer(userInfo, question, answer) {
    return dispatch => {
      dispatch({
        type: actionTypes.APPLY_ANSWER,
        userInfo,
        question
      });
      API.applyAnswer(userInfo, question, answer)
        .then(({userInfo, reaction }) => actions.showAnswerResult(userInfo, question, answer, reaction)(dispatch))
        .catch(getApiErrorLogger('applyAnswer'));
    };
  },


  showAnswerResult(userInfo, question, answer, reaction) {
    return dispatch => dispatch({
      type: actionTypes.SHOW_ANSWER_RESULT,
      userInfo,
      question,
      reaction,
      answer
    });
  },


  fetchUserResult(userInfo) {
    return dispatch => {
      dispatch({
        type: actionTypes.FETCH_USER_RESULT,
        userInfo
      });
      API.fetchUserResult(userInfo)
        .then(response => actions.showUserResult(response.userInfo, response.userResult)(dispatch))
        .catch(getApiErrorLogger('fetchUserResult'));
    };
  },


  showUserResult(userInfo, userResult) {
    return dispatch => dispatch({
      type: actionTypes.SHOW_USER_RESULT,
      userInfo,
      userResult
    });
  },


  tryAgain(userInfo) {
    const newUserInfo = Object.assign({}, userInfo);
    newUserInfo.aValue = 0;
    newUserInfo.bValue = 0;
    newUserInfo.score = 0;
    return actions.fetchQuestion(newUserInfo);
  },


  fetchResults() {
    return dispatch => {
      dispatch({ type: actionTypes.FETCH_RESULTS })
      API.fetchResults()
        .then(results => actions.showResults(results)(dispatch))
        .catch(getApiErrorLogger('fetchResults'));
    };
  },


  showResults(results) {
    return dispatch => dispatch({
      type: actionTypes.SHOW_RESULTS,
      results
    });
  }

});






