import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './AnswerResult.css';
import { MainImage } from './../MainImage';
import { Button } from './../Button';


export class AnswerResult extends Component {

  render() {
    const reaction = this.props.reaction;
    const hasImage = !!reaction.img || !!reaction.sticker;
    const textClass = 'AnswerResult__text' + (hasImage ? ' indent_vert' : '');

    return <div className="block AnswerResult">
      { hasImage && <MainImage src={reaction.img} sticker={reaction.sticker}/> }

      <div className={textClass}>
        { reaction.text }
      </div>

      <div className="indent_vert">
        <Button text="Продолжить" onPress={this.props.onNextQuestion}/>
      </div>
    </div>;
  }

}


AnswerResult.propTypes = {
  reaction: PropTypes.object,
  onNextQuestion: PropTypes.func
};



